import React from "react";
import PropTypes from "prop-types";
import "./movie.css";

function Movie({ title, summary, poster, year, genres }) {
  return (
    <div className="movies__movie">
      <img src={poster} alt={title} title={title} className="poster" />
      <div className="movie__data">
        <h3 className="movie__title">{title}</h3>
        <h5 className="movie__year">{year}</h5>
        <ul className="genres">
          <span className="genres__name">genre:</span>
          {genres.map((genre, number) => (
            <span key={number} className="genres__genre">
              {genre}
            </span>
          ))}
        </ul>
      </div>
      <div className="movie__summary">{summary.slice(0, 180)}...</div>
    </div>
  );
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  year: PropTypes.number.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Movie;
